*{


  
    margin: 0%;
    background: rgb(255,251,0);
    background: radial-gradient(circle, rgba(255,251,0,1) 0%, rgba(239,231,68,1) 100%);
}



.App {
  text-align: center;
  color: aliceblue;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.img-container{

 height: 100px;
 width: 100px;
 display: flex;
 flex-direction: row;
 flex-wrap: wrap;
 justify-content: center;
 align-content: center;
}

.full-size{



}

.img-central {
  position: relative;
  top: 15%;
  bottom: 15%;
  display: flex;
  /*border: 2px solid green;*/
  flex-direction: row;
  gap: 1.2em;
  background-color: white;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: stretch;
  padding-bottom: 15%;
}
img.img-box {
  height: 100%;
  width: 14rem;
  object-fit: scale-down;
  border-radius: 6%;
  border: 2px solid white;
  background-color: white;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.son {
  display: flex;
  flex-grow: 0;
  flex-shrink: 1;
  position: relative;
  flex-wrap: wrap;
  height: 14rem;
  border-radius: 6%;
  flex-basis: auto;
  background-color: white;
  align-self: auto;
  order: 0;
  left: 0%;
  gap: 3%;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

#modal-img {
  position: absolute;
  background-color: #1b1717e0;
  height: 100%;
  z-index: 100;
  width: 100%;
  /* border: 4px solid red; */
  visibility: hidden;
}

#close {
  width: 10%;
  position: absolute;
  right: 0px;
  background-color: rgb(38, 255, 0);
  visibility: hidden;
  text-decoration: none;
}

#modal-imgs {
  width: 80%;
  position: absolute;
  right: 0px;
  object-fit: scale-down;
  height: 100%;
  left: 9%;
  background-color: white;
  visibility: hidden;
}

.search-bar {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  position: relative;
  top: 7%;
}

input[type="text"] {
  height: 1.8rem;
  width: 20rem;
  background: white;
}

#loading{

height: 10rem;
border-radius: 50%;



} 


svg:hover {
  color: red;
}